import { Component, OnInit } from '@angular/core';
import { AlertController, ModalController, ToastController } from '@ionic/angular';
import { ApiProductosService } from 'src/app/services/api-productos.service';
import { ModalProductPage } from '../modal-product/modal-product.page';

import Fuse from 'fuse.js';
import { Producto } from 'src/app/Models/Productos';
import { CarritoServiceService } from 'src/app/services/carrito-service.service';
import { Usuario } from 'src/app/Models/usuario.interface';
import { CheckoutServiceService } from 'src/app/services/checkout-service.service';
import { LoginService } from 'src/app/services/login.service';
import { Router } from '@angular/router';
import { ServiciowishlistService } from 'src/app/services/serviciowishlist.service';



@Component({
  selector: 'app-cart',
  templateUrl: './cart.page.html',
  styleUrls: ['./cart.page.scss'],
})
export class CartPage implements OnInit {


  public product: any;
  public searchbarVisible: boolean = false;
  public txtBuscador: string = '';
  public producto : any =[];
  usuario!: Usuario;
  public grandTotal!: number ;
  public color!: string;
  public productos: any = [];
  totalItems: any;
  public resultadoBusqueda: any = null;
  compra = false;
  public numeroResultados: number = 0;
  public url: string = 'https://lideart.com.mx/';
  codigo: any;
  cupones: any =[];
  porcentaje; any;
  descuento= "no";
  mensaje: any;
  calculo:any;
  porcentajeFinal:any;
  cancelar = false;
  haydescuento = false;


  items: Producto[]=[];
  public options = {
    // isCaseSensitive: false,
    // includeScore: false,
    shouldSort: true,
    // includeMatches: false,
    // findAllMatches: false,
    minMatchCharLength: 5,
    // location: 0,
    threshold: 0.5,
    distance: 60,
    // useExtendedSearch: false,
    // ignoreLocation: false,
    // ignoreFieldNorm: false,
    // fieldNormWeight: 1,
    keys: ['nombre'],
  };

  datosUsuario: any = [];
  nombre!: string;
  apellido!: string;

  constructor(
    private apiProductos: ApiProductosService,
    public modalController: ModalController,
    private shoppingCart: CarritoServiceService,
    public toastController: ToastController,
    private checkoutService: CheckoutServiceService,
    private usuarioService: LoginService,
    public alertController: AlertController,
    private router: Router,
    private servicioWishlist: ServiciowishlistService
  ) {
    this.usuarioService.usuario.subscribe(res => {
      this.usuario = res;

    });
    this.usuarioService.usuario.subscribe(res =>{
      this.usuario = res;
      if (this.usuario) {
        this.checalo();
      }
    });
  }
  

  ngOnInit() {
    this.apiProductos.getProductos().subscribe((response) => {
      this.productos = response;
    });
    this.shoppingCart.cartItems.subscribe(data=>{
      this.items = data;
      this.totalItems = data.length;
      if (this.items){
        this.getTotal(this.items);

      }
    });

  }

  checalo() {
    this.usuarioService.checaUsuario(this.usuario.usuario).subscribe(data => {
      this.datosUsuario = data;


      this.nombre = this.datosUsuario.nombre;
      this.apellido = this.datosUsuario.apellido;
     
    })
  }

  openSearchBar() {
    var buscador = document.getElementById('buscador');
    if(buscador){
      buscador.style.display = "block";
    }
    this.txtBuscador = '';
    this.searchbarVisible = this.searchbarVisible == false ? true : false;
    if (this.searchbarVisible == false){
      buscador.style.display = "none";
    }
  }



  search(event: any) {
    if (event.detail.value != '') {
      const filtro = (event.target as HTMLInputElement).value.trim();
      const fuse = new Fuse(this.productos, this.options);

      const pattern = filtro;
      this.resultadoBusqueda = fuse.search(pattern);
      this.numeroResultados = fuse.search(pattern).length;
      this.resultadoBusqueda = this.resultadoBusqueda.slice(0, 100);
    }
  }

  async openModal(id: number) {
    this.txtBuscador = '';

    this.apiProductos.getSpecificProduct(id).subscribe(async (response) => {
      if (response[0]) {
        this.product = response[0];
        console.log(this.product);
        const modal = await this.modalController.create({
          component: ModalProductPage,
          cssClass: 'my-custom-class',
          initialBreakpoint: 0.6,
          breakpoints: [0, 0.6, 0.8, 1],
          componentProps: {
            product: this.product,
          },
        });
        return await modal.present();
      }
    });
  }

  getTotal(data: any){
    let subs = 0;

    for (const item of data) {
      subs+= item.precio * item.quantity;

      this.grandTotal = subs;
      
    }
  }

  onDelete(i: number){
    this.items.splice(i,1);
    this.shoppingCart.setCartData(this.items);
    this.getTotal(this.items);
  }

  validateInput(event:any, i:number){
    const qty = +event.target.value;
    if (qty <1) {
      event.target.value = this.items[i].quantity;
      return;
    }

    if(this.items[i].existencia < qty){
      this.ShowError();
      -event.target.value;
      this.compra = false;
    }else{
      this.QtyUpdated(qty,i);
      this.compra= true;
    }
  }

  private QtyUpdated(qty: number, i:number){
    this.items[i].quantity = qty;
    this.shoppingCart.setCartData(this.items);
    this.getTotal(this.items);
  }

  vaciaCarrito(){
    this.shoppingCart.deleteCart();
  }

  async ShowError() {
    const toast = await this.toastController.create({
      message: 'Necesitas Seleccionar Color',
      color: "danger",
      position: "top",
      duration: 1500
      
    });
    toast.present();
  }

  registraCarrito(){

    var jsonCarrito = {"total": this.grandTotal, "email": this.usuario.usuario, "descuento": this.haydescuento,"porcentaje":this.porcentaje};
    this.checkoutService.registraCarrito(jsonCarrito).subscribe(data=>{
      this.router.navigate(['/checkout']);
    })

  }


  checamelcupon(){
    var jsonCupon = {"codigo": this.codigo, "email": this.usuario.usuario,"total": this.grandTotal.toFixed(2)};
    this.apiProductos.checaCupon(jsonCupon).subscribe(codigos =>{
      this.cupones = codigos;
      this.descuento = this.cupones.descuento;

      if (this.descuento == "si") {
        this.porcentaje = parseInt(this.cupones.porcentaje);
        this.porcentaje = this.porcentaje/100;
        this.calculo =  this.grandTotal*this.porcentaje;
        this.grandTotal = this.grandTotal-this.calculo;
        this.cancelar = true;
        this.haydescuento = true;
        this.sisearmo()
      }
      if (this.descuento == "no") {
        this.mensaje = this.cupones.mensaje;
        this.correoNoExiste();
      }
      
    })
  }

  async correoNoExiste() {
    const alert = await this.alertController.create({
      cssClass: 'loginAlertComp',
      header: 'Error',
      //subHeader: 'Subtitle',
      message: this.mensaje,
      buttons: ['OK']
    });

    await alert.present();

    const { role } = await alert.onDidDismiss();
    
  }

  async sisearmo() {
    const alert = await this.alertController.create({
      cssClass: 'loginAlertComp',
      header: 'Exito',
      //subHeader: 'Subtitle',
      message: 'El cupon a sido aplicado',
      buttons: ['OK']
    });

    await alert.present();

    const { role } = await alert.onDidDismiss();
    
  }

  async siloborrewe() {
    const alert = await this.alertController.create({
      cssClass: 'loginAlertComp',
      header: 'Exito',
      //subHeader: 'Subtitle',
      message: 'El cupon a sido cancelado',
      buttons: ['OK']
    });

    await alert.present();

    const { role } = await alert.onDidDismiss();
    
  }

  borramelcupon(){
    var jsonCupon = {"codigo": this.codigo, "email": this.usuario.usuario};
    this.apiProductos.borraCupon(jsonCupon).subscribe(cuponsito =>{
    this.grandTotal = this.grandTotal+this.calculo;
    this.cancelar = false;
    this.haydescuento = false;
    this.descuento = "no";
    this.siloborrewe()
    })
  }


  agregarWishlist(id_product:any, title:any, sku:any, quantity:any, image_link:any, color:any, medida:any){
    var jsonWishlist = {
      "usuario": this.usuario.usuario,
      "id_product": id_product,
      "title": title,
      "sku": sku,
      "quantiity": quantity,
      "imagen": image_link,
      "sku_imagen":sku,
      "color": color,
      "medida": medida,
      "link": ""
    }
    this.servicioWishlist.sendWishlist(jsonWishlist).subscribe(data =>{
      if (data == 0) {
          this.yaexiste();
      }else{
     this.agregado();
      }
     })

}

async yaexiste() {
  const toast = await this.toastController.create({
    message: 'Este Producto ya se encuentra en tu Wishlist.',
    color: "danger",
    position: "top",
    duration: 1500
    
  });
  toast.present();
}

async agregado() {
  const toast = await this.toastController.create({
    message: 'Este Producto se agrego a tu Wishlist.',
    color: "success",
    position: "top",
    duration: 1500
    
  });
  toast.present();
}

async noagregado() {
  const toast = await this.toastController.create({
    message: 'Este Producto se agrego a tu Wishlist.',
    color: "success",
    position: "top",
    duration: 1500
    
  });
  toast.present();
}

async presentAlertConfirm(id_product:any, title:any, sku:any, quantity:any, image_link:any, color:any, medida:any) {
  const alert = await this.alertController.create({
    cssClass: 'my-custom-class',
    header: 'Wishlist',
    message: '¿Deseas añadir a la lista de deseos?',
    buttons: [
      {
        text: 'Cancelar',
        role: 'cancel',
        cssClass: 'secondary',
        handler: (blah) => {
          // console.log('Confirm Cancel: blah');
        },
      },
      {
        text: 'Confirmar',
        handler: () => {
        this.agregarWishlist(id_product, title, sku, quantity, image_link, color, medida);
        },
      },
    ],
  });

  await alert.present();
}
}
